@import "ScssHelpers/helpers-all.scss";

@mixin link_styles {
	position: relative;

	color: $gold_2;
	font-weight: 500;
	line-height: 1.16;

	@include bp($point_2, min) {
		font-size: 1.4rem;
		letter-spacing: -.04em;
		border-bottom: 2px solid $gold_2;
	}
	@include bp($point_3) {
		font-size: 1.3rem;
		letter-spacing: -.02em;
		border-bottom: 1px solid $gold_2;
	}
}

.product_details {
	padding-bottom: 3rem;

	@include bp($point_2, min) {
		padding-top: 6.8rem;
	}
	@include bp($point_3) {
		padding-top: 3.8rem;
	}
}

.product_details__title {
	@include h3;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.2rem;
		}
		@include bp($point_3) {
			margin-bottom: .6rem;
		}
	}
}

.product_details__row {
	@include bp($point_2, min) {
		margin: 0 -11.2rem -4rem 0;

		@include flex_block(row, wrap, flex-start, stretch);
	}
}

.product_details__column {
	@include bp($point_2, min) {
		$offset-right: 11.6rem;
		$offset-bottom: 4rem;

		padding: 0 $offset-right $offset-bottom 0;

		&:nth-child(2n + 1) {
			position: relative;

			width: 64%;

			&::after {
				content: "";
				position: absolute;
				right: calc($offset-right / 2);
				top: 0;

				width: 1px;
				height: calc(100% - $offset-bottom);

				background-color: $beige_2;
			}
		}
		&:nth-child(2n + 2) {
			width: 36%;
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2.6rem;
			padding-bottom: 2.4rem;

			border-bottom: 1px solid $beige_2;
		}
	}
}

.product_details__wrap {
	strong {
		display: block;

		color: $black;
		font-weight: 500;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.4rem;
			}
			@include bp($point_3) {
				margin-bottom: .8rem;
			}
		}

		@include bp($point_2, min) {
			font-size: 1.8rem;
			line-height: 1.56;
		}
		@include bp($point_3) {
			font-size: 1.4rem;
			letter-spacing: -.034em;
		}
	}

	p:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 0.8rem;
		}
		@include bp($point_3) {
			margin-bottom: 4px;
		}
	}

	@include text;

	@include bp($point_2, min) {
		padding-top: 4px;
		line-height: 1.5;
	}
	@include bp($point_3) {
		line-height: 1.43;
		letter-spacing: -.014em;
	}
}

.product_details__item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3rem;
		}
		@include bp($point_3) {
			margin-bottom: 2.6rem;
		}
	}

	@include flex_block(row, nowrap, flex-start);
}

.product_details_img {
	display: block;
	flex-shrink: 0;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: 2.4rem;
		}
		@include bp($point_3) {
			margin-right: 1.6rem;
		}
	}

	@include bp($point_2, min) {
		width: 5.6rem;
		height: 5.6rem;
	}
	@include bp($point_3) {
		width: 4.6rem;
		height: 4.6rem;
	}
}

.product_details_img__in {
	@include cover_img;
}

.product_details_info__title {
	color: $gray_2;
	font-weight: 500;
	letter-spacing: 0.11em;
	text-transform: uppercase;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 0.6rem;
		}
		@include bp($point_3) {
			margin-bottom: 3px;
		}
	}

	@include bp($point_2, min) {
		font-size: 1.8rem;
		line-height: 1.33;
	}
	@include bp($point_3) {
		font-size: 1.4rem;
		line-height: 1.14;
	}
}

.product_details_info__text {
	color: $black;

	&:not(:last-child) {
		margin-bottom: 0.6rem;
	}

	@include bp($point_2, min) {
		font-size: 1.4rem;
		line-height: 1.57;
	}
	@include bp($point_3) {
		font-size: 1.3rem;
		line-height: 1.23;
	}
}

.product_details_info__item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.2rem;
		}
		@include bp($point_3) {
			margin-bottom: 4px;
		}
	}
}

.product_details_info__link {
	&:hover,
	&:focus {
		border-color: transparent;
		text-decoration: none;
	}

	transition: border-color .3s ease;

	@include link_styles;
}

.product_details_info_link {
	display: inline-flex;
	align-items: center;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.product_details_info_link__text {
	transition: border-color .3s ease;

	.product_details_info_link:hover &,
	.product_details_info_link:focus & {
		border-color: transparent;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: 0.5rem;
		}
		@include bp($point_3) {
			margin-right: 4px;
		}
	}

	@include link_styles;
}

.product_details_info_link__icon {
	flex-shrink: 0;

	color: $gold_2;

	@include bp($point_2, min) {
		width: .5rem;
		height: 1.1rem;
	}
	@include bp($point_3) {
		margin-top: 1px;
		width: .6rem;
		height: .9rem;
	}
}
