@import "ScssHelpers/helpers-all.scss";

.info_acc {
}

.info_acc__list {
	margin: 0;
	padding: 0;

	list-style: none;
}

.info_acc__item {
	border-bottom: 1px solid $beige_2;

	@include bp($point_2, min) {
		padding: 3.4rem 0 4.2rem;
	}
	@include bp($point_3) {
		padding: 1.8rem 0 2rem;
	}
}

.info_acc__head {
	cursor: pointer;

	@include flex_block(row, nowrap, flex-start, center, center);
}

.info_acc__title {
	@include h3;

	color: $black;

	&:not(:last-child) {
		margin-right: 2rem;
	}
}

.info_acc__icon {
	flex-shrink: 0;

	color: $gold_2;
	transition: transform .3s ease, color .3s ease;

	&:hover,
	&:focus{
		color: $black;
	}

	.info_acc__item--active_state & {
		transform: rotate(-180deg);
	}

	@include bp($point_2, min) {
		width: 3.2rem;
		height: 1.6rem;
		margin: 0.7rem 1px 0 auto;
	}
	@include bp($point_3) {
		width: 1.8rem;
		height: .8rem;
		margin: 2px 3px 0 auto;
	}
}

.info_acc__body {
	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	overflow: hidden;
	pointer-events: none;

	.info_acc__item--active_state & {
		opacity: 1;
		pointer-events: auto;
	}
}

.info_acc__wrap {
	padding-top: 3rem;
}

.info_acc__text {
	line-height: 1.5;
	color: $black;

	p:not(:last-child) {
		margin-bottom: 1rem;
	}

	@include text;
}
